.campus{
  margin: 80px auto;
  width: 85%;
  text-align: center;
}
.gallery{
  display: flex;
  align-items: center;
justify-content: space-between;
margin-bottom: 40px ;
}

.gallery img{
  width: 23%;
  border-radius: 10px;
}

/* media quer */
 @media (max-width:800px){
  .gallery{
    flex-wrap: wrap;
  }

  .gallery img{
    width: 48%;
    margin-bottom: 30px;
  }
 }