.about{
  margin: 100px auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
width: 90%;
}

.about-left{
flex-basis: 40%;
position: relative;
}
.about-right{
  flex-basis: 56%;
  
}
.about-img{
width: 100%;
border-radius: 10px;
position: relative;
}
.play-icon{
  width: 60px; 
  position: absolute;
  top: 50%;
  left: 50%;
  transform:translate(-50% ,-50%) ;
  cursor: pointer;
}

.about h3{

  color: #212ea0;
  font-size: 16px;
  font-weight: 600;
}

.about h2{

  color: #000f38;
  font-size: 35px;
margin: 10px 0;
max-width: 400px;
}
.about p{

  color: #676767;
  font-size: 15px;
margin-bottom: 15px;

}

 /* mediaquer */

 @media (max-width:800px){
  .about{
    flex-direction: column;
  }
  .about-left{
    flex-basis: 100%;
    margin: 20px;
    order: 2;
  }

  .about-right{
    flex-basis: 100%;
    margin: 20px;
  }
 }