nav{
  width: 100%;
  /* background-color: #333; */
  color: white;
  padding: 4px 0;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 10;
}



.logo{
  width: 180px;
}

nav ul li{
  display: inline-block;
  list-style: none;
  font-size: 16px;
  margin: 5px 20px;
}
.dark-nav{
  background: #212ea0;
  transition: 1s;
}

.menu-icon{
  display: none;
}
/* mediaquery */

@media (max-width : 1000px){

.logo{
  width: 140px;
}
nav ul li{
  margin: 10px 15px;
}

}
@media (max-width : 850px){

  nav{
    padding: 15px 0;
  }
nav ul {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  background: #212ea0;
  z-index: -1;
  width: 200px;
  padding-top: 70px;
  transition: 1s;

}
nav ul li{
  display: block;
  margin: 25px 32px;
}

.menu-icon{
  
  display: block;
  width: 30px;
  cursor: pointer;
}

.hide-mobile-menu{
  right: -200px;
}
}